.individFurnitere {
  margin-bottom: 296px;
}

.individFurnitereContainer {
  position: relative;
  background-color: #5b5b5b;
  max-height: 453px;
}

.individFurnitereBox {
  position: relative;
  display: block;
  margin: 0 auto;
  max-width: 515px;
}

.rectangleCF1 {
  position: absolute;
  content: "";
  background-color: #f6973f;
  top: -35px;
  right: 0px;
  min-width: 92px;
  min-height: 70px;
}
.rectangleCF1__text {
  font-size: 10px;
  line-height: 1.3;
  letter-spacing: 0.25em;
  font-weight: 300;
  position: absolute;
  bottom: 11px;
  left: 5px;
  color: #f9f9f9;
}

.individFurnitereTextBox {
  padding: 0 18px;
}

.sectionTitle {
  font-size: 22px;
  line-height: 1;
  font-weight: 700;
  color: #f9f9f9;
  padding: 78px 0 20px 0;
}
.sectionTitle__dark {
  font-size: 22px;
  line-height: 1;
  font-weight: 700;
  color: #2c2b29;
  padding: 78px 0 20px 0;
}
.sectionTitle__ourHistory {
  position: absolute;
  top: -52;
  left: 18px;
  padding: 0;
}
.sectionTitle__lastLublic {
  padding: 0 0 32px 0;
}

.sectionSubtitle {
  font-size: 18px;
  line-height: 1.3;
  letter-spacing: 0.08em;
  font-weight: 500;
  color: #f9f9f9;
  padding-bottom: 12px;
}
.sectionSubtitle__ourHistory {
  padding-top: 48px;
}
.letter {
  color: #f6973f;
}

.sectionText {
  font-size: 14px;
  line-height: 1.4;
  letter-spacing: 0.15em;
  font-weight: 300;
  color: #f9f9f9;
  padding-bottom: 68px;
}

.ifImg {
  position: relative;
  top: 0px;
  width: 340px;
  height: 239px;
  background-color: rgb(171, 166, 206);
  margin: 0 auto;
  margin-bottom: 92px;
  z-index: 1;
}

.rectangleCF2 {
  position: absolute;
  bottom: -59px;
  right: 0px;
  background-color: #e7e7e7;
  min-width: 287px;
  min-height: 260px;
}

.gridCF {
  position: absolute;
  top: 0;
  left: -1px;
  min-width: 287px;
  min-height: 260px;
}

.rectangleCF3 {
  position: absolute;
  content: "";
  background-color: #f6973f;
  bottom: -16px;
  left: 55px;
  min-width: 67px;
  min-height: 42px;
}

.btn {
    font-size: 14px;
    line-height: 1.5;
    letter-spacing: 0.08em;
    font-weight: 500;
    color: #f9f9f9;
    text-transform: uppercase;
    padding: 11px 23px;
    margin: 0 auto;
    background-color: #f6973f;
    outline: none;
    border: none;
    border-radius: 4px;
    cursor: none;
    display: block;
  
    &:active {
      box-shadow: inset 0px 0px 3px 3px rgba(0, 0, 0, 0.1),
        inset 0px 10px 15px -3px rgba(0, 0, 0, 0.1);
      background-color: #dd8839;
    }
  }